.back{
  display: flex;
  align-items: center;
  justify-content: flex-start;

  box-sizing: border-box;
  width: 100%;

  padding-left: 32px;
  padding-top: 33px;

  text-align: left;

  img{
    padding-right: 20px;
  }

  h1{
    font-size: 14px;
    font-weight: normal;
    line-height: 16px;

    max-width: 245px;
    width: 100%;

    color: #3C3C3C;
  }

  .colors{
    padding-right: 20px;
    width: 24px;
    height: 21px;
  }

  @media screen and (min-width: 429px) {

    display: flex;
    padding: 0px 0px;
    width: auto;

    img{
      height: 44px;
      padding-right: 40px;
    }

    h1{
      font-size: 28px;
      line-height: 32px;

      max-width: 509px;
    }

    .colors{
      padding-right: 40px;
      width: 50px;
      height: 42px;
    }
  }

  @media screen and (max-width: 376px){
    left: 7px;
  }

}

.test{
  display: flex;
  align-items: center;
  justify-content: flex-start;

  box-sizing: border-box;

  position: fixed;
  bottom: 125px;

  padding: 18px 45px;

  max-width: 330px;

  text-align: left;

  color: #ffffff;
  background: #3D7FFF;
  border-radius: 33.5px;

  h1{
    font-size: 18px;
    font-weight: bold;
  }

  .colors{
    padding-right: 10px;
    path{
      fill: #ffffff;
    }
  }

  @media screen and (min-width: 429px) {
    display: flex;
    padding: 0px 0px;

    width: 100%;
    max-width: 600px;

    background: none;

    position: unset;


    img{
      height: 44px;
      padding-right: 40px;
    }

    h1{
      font-size: 28px;
      font-weight: normal;

      line-height: 32px;

      color: #3C3C3C;
    }
    .colors{
      width: 50px;
      height: 42px;
      padding-right: 40px;
      path{
        fill: #3D7FFF;
      }
    }
  }


}